export const EDITOR = {
    CHECK_UNRELEASE_FAILED: '',
    RELEASE_FAILED_FOR_UNSAVED: '',
    UNSAVED_ORDER: '',
    RELEASE_FAILED_FOR_UNGENERATED_PAGE: '',
    RELEASE_FAILED: '',
    RELEASE_FAILED_FOR_TOO_MUCH_HOTZONE: '100',
    RELEASE_FAILED_FOR_TOO_MUCH_ID: 'ID',
    RELEASE_FAILED_FOR_TOO_MUCH_INSTANCE: '100',
    RELEASE_FAILED_FOR_DUPLICATE_LINK: '',
    RELEASE_FAILED_FOR_ERRORMAP: (errorMap) => {
        const messages = [];
        messages.push(`<p>${errorMap.floorId}</p>`);
        messages.push(`<p>${errorMap.advertGroupName}(id:${errorMap.advertGroupId})</p>`);
        messages.push(`<p>${errorMap.errorStageName}(id:${errorMap.errorStageId})</p>`);
        if(errorMap.minNum) {
            messages.push(`<p>${errorMap.minNum}</p>`);
        } else if(errorMap.errorAdvertId) {
            messages.push(`<p>id${errorMap.errorAdvertId}</p>`);
            messages.push(`<p>${errorMap.standardSize}</p>`);
        }
        return messages.join('');
    },
    RELEASE_FAILED_FOR_INCONSISTENT: () => {
        const content = [];
        content.push('<p><p>');
        content.push('<p></p>');
        return content.join('');
    },
    UNRELEASE: '',
    SAVE_FAILED_FOR_ERRORS: '',
    SAVE_FAILED_ERRORS_CONTENT: (viewInstanceInfo, subViewInstanceInfo, errMessage, subErrMessage) => {
        const messages = [];
        messages.push(`<p>${viewInstanceInfo.name}</p>`);

        if(subViewInstanceInfo.length > 0) {
            const msg = subViewInstanceInfo.map((info)=>info.name).join(',');
            messages.push(`<p>${msg}</p>`);
        }else {
            if(!!errMessage) {
                messages.push(`<p>${errMessage}</p>`);
            } else if(!!subErrMessage) {
                messages.push(`<p>${subErrMessage}</p>`);
            }
        }

        return messages.join('');
    },
    EXISTED_UNRELEASE_CONTENT: (lastUpdateErpNo,lastUpdateTime) => {
        const messages = [];
        messages.push('<p></p>');
        messages.push(`<p>${lastUpdateErpNo} ${lastUpdateTime}</p>`);
        messages.push('<p></p>');
        return messages.join('');
    },
    FORBID_INSERT_VIEW_INSTANCE_IN_BIVIEWMODE: 'BI',
    SAVE_VIEW_INSTANCE_FIRST: '',
    RELEASE_FAILED_FOR_BI: 'BI',
    RELEASE_FAILED_FOR_BLOCK_NAME_EMPTY: '',
    RELEASE_FAILED_FOR_LABELLIST_EMPTY: '0',
    RELEASE_FAILED_FOR_ANCHOR: 'BI',
    RELEASE_FAILED_FOR_INCONTINUOUS: (names) => {
        const messages = [];
        messages.push(`<p>${names.join(',')}</p>`);
        messages.push(`<p></p>`);
        return messages.join('');
    },
    RELEASE_FAILED_FOR_NO_BI: (name) => {
        const messages = [];
        messages.push(`<p>${name}</p>`);
        return messages.join('');
    },
    RELEASE_FAILED_FOR_NOT_COMPLETE: (name) => {
        const messages = [];
        messages.push(`<p>${name}</p>`);
        return messages.join('');
    },
    RELEASE_FAILED_FOR_SORT: (name) => {
        const messages = [];
        messages.push(`<p>BI</p>`);
        messages.push(`<p>${name}</p>`);
        return messages.join('');
    },
    RELEASE_FAILED_FOR_BI_ERROR: 'BI'
}

